<template>
    <div>
        <div class="login_top">
            <van-nav-bar
                title="注册"
                left-arrow
                @click-left="onClickLeft"
            />
        </div>

        <div class="login_main">
            <div class="top">
               
                <van-form @submit="onSubmit">
                    <van-field
                        v-model="username"
                        name="username"
                        label="用户名"
                        placeholder="用户名"
                        :rules="[{ required: true, message: '请填写用户名' }]"
                    />
                    <van-field
                        v-model="password"
                        type="password"
                        name="password"
                        label="密码"
                        placeholder="密码"
                        :rules="[{ required: true, message: '请填写密码' }]"
                    />
                     <van-field
                        v-model="password2"
                        type="password"
                        name="password2"
                        label="确认密码"
                        placeholder="密码"
                        :rules="[{ required: true, message: '请填写密码' }]"
                    />
                    <div style="margin: 16px;">
                        <van-button round block type="info" native-type="submit">
                        提交
                        </van-button>
                    </div>
                    </van-form>
            </div>
           
        </div>

  

    </div>
</template>

<script>
import { Toast } from 'vant';
    export default {
         data() {
            return {
            username: '',
            password: '',
            password2: '',
            };
        },
        methods: {
            onClickLeft() {
                this.$router.go(-1)
            },
             onSubmit(params) {
                //  定义一个变量存放this的指向
                 let _this = this
                
                // http://127.0.0.1:3000/login?username=zhangsan&password=123123&password2=123123
                console.log(this)
                this.$axios.get("register",{params}).then((res)=>{
                    console.log(res)
                    if(res.data == "200"){
                        Toast({
                            message:"注册成功",
                            onOpened(){
                                _this.$router.push("/login")
                            }
                        });


                    }else{
                        Toast('注册失败');
                    }
       
                })
   
            },
        }
    }
</script>

<style lang="scss" scoped>
.login_main{
    background-color: white;
    padding: 20px;
    padding-top: 40px;
    margin:  0 auto;
    font-size: 16px;
    .top{
        padding-bottom: 20px;
        border-bottom: 1px solid #f0f0f0;
    }
    
  
    
   
}
</style>